
import { InfoFilled } from '@element-plus/icons-vue'
import { defineComponent } from '@vue/runtime-dom'
export default defineComponent({
  name: "deleteButton",
  components: {},
  setup(props, { emit }) {
    const confirmEvent = () => {
      emit('confirmEvent')
      console.log('confirm!')
    }
    const cancelEvent = () => {
      console.log('cancel!')
    }
    return {
      InfoFilled, cancelEvent, confirmEvent
    }
  }
})
